@use "base/mixins" as m;
@use "base/variables" as v;

.header {
  background-color: v.$verde;

  @include m.tablet {
    &.fijo {
      left: 0;
      top: 0;
      position: fixed;
      width: 100%;
      box-shadow: .1rem .2rem .3rem #333;
    }
  }


  h1 {
    color: v.$blanco;
    text-align: center;

    @include m.tablet {
      margin-bottom: 0;
    }
  }

  .contenido-header {
    @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }

    padding: 2rem;

  }
}

.navegacion-principal {
  @include m.tablet {
    display: flex; 
    gap: 2rem;
  }

  a {
    color: v.$blanco;
    font-size: 2.4rem;
    display: block;
    text-align: center;
    margin-bottom: 1rem;

    @include m.tablet {
      margin-bottom: 0;
    }

    &:hover {
      color: v.$amarillo;
    }
  };
}


















